<template>
    <button 
        :type="action"
        :class="type"
        :disabled="disabled"
        @click="callback($event)"
        >
        <slot></slot>
    </button>
</template>

<style>
button {
    font-family: sans-serif;
    display: inline-block;
    font-size: 1em;
    font-weight: 400;
    height: 3.125em;
    letter-spacing: 0.05em;
    line-height: 3.125em;
    padding: 0 2.5em;
    border-radius: 3px;
    border: 0;
    opacity: 0.85;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -moz-appearance: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    appearance: none;
    -moz-transition: 0.2s;
    -webkit-transition: 0.2s;
    -ms-transition: 0.2s;
    transition: 0.2s;
}

        button:hover {
            opacity: 0.7;
        }

        button:active {
            opacity: 1;
        }
        
    button.danger {
        background-color: #ed4933;
        box-shadow: none !important;
        color: #ffffff !important;
    }

    button.success {
        background-color: #007A5E;
        box-shadow: none !important;
        color: #ffffff !important;
    }

    button.green {
        background-color: #7fc241;
        box-shadow: none !important;
        color: #ffffff !important;
    }

    button.warning {
        background-color: #f68b1e;
        box-shadow: none !important;
        color: #ffffff !important;
    }

    button.orange {
        background-color: #f1b016;
        box-shadow: none !important;
        color: #ffffff !important;
    }

    button.menu {
        background-color: transparent;
        color: #fff;
        border: none;
        font-size: 1.3em;
        font-weight: lighter;
        position: fixed;
        right: 1em;
        top: 0.8em;
        -moz-filter: brightness(100%);
        -webkit-filter: brightness(100%);
        -ms-filter: brightness(100%);
        filter: brightness(100%);
        cursor: pointer;
        -moz-transition: 0.45s;
        -webkit-transition: 0.45s;
        -ms-transition: 0.45s;
        transition: 0.45s;
        z-index: 3;
    }

        button.menu i:before {
            -moz-transition: 0.45s;
            -webkit-transition: 0.45s;
            -ms-transition: 0.45s;
            transition: 0.45s;
            opacity: 1;
        }
        
        button.menu.active  {                    
            -moz-filter: brightness(0%);
            -webkit-filter: brightness(0%);
            -ms-filter: brightness(0%);
            filter: brightness(0%);
        }
        
        button.menu.active i:before {
            opacity: 0;
        }

    button.slim {
        display: block;
        margin: 0 0 1em 0;
        width: 100%;
    }

    button.s {
        font-size: 0.8em;
    }

    button.l {
        font-size: 1.35em;
    }

    button.disabled,
    button:disabled {
        opacity: 0.25;
        pointer-events: none;
        -moz-pointer-events: none;
        -webkit-pointer-events: none;
        -ms-pointer-events: none;
    }
</style>

<script>
export default {
    name: 'Btn',
    props: {
        type: String,
        action: String,
        disabled: Boolean
    },
    methods: {
        callback: function(e) {
            this.$emit('click', e);
        }
  }
}
</script>